import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../../UiResources/events';

export default class ExecutionFlowOrganism extends AtomicDesign {

    getBlockByType(type){
        if(this.vue.selectedItems?.blocks){
            return this.vue.selectedItems.blocks.find (block => block.name == type);
        }
        return {};
    }

    getCaptionByBlockType(type){
        const block = this.getBlockByType(type)
        if(block?.data?.args0){
            const field = block.data.args0.find(a=>a.type == "field_label_serializable");
            if (field){
                return field.text;
            }
        }
        return "";
    }

    viewLogs(logs){
        this.emitGlobalEvent(EVENTS.DIALOG.SHOW_LOGS, {logs});
    }

    viewAllLogs(){
        const logs = this.vue.executionFlowData.flatMap(item=>item.log).filter(item => item !== null);
        this.emitGlobalEvent(EVENTS.DIALOG.SHOW_LOGS, {logs});
    }
}