<template>
  <div class="execution-flow-organism">
   
    <div v-if="!executionFlow" class="execution-flow-organism__message">
      <p>To show an Execution Flow you must run an API. </p>
      <p>Please select an API and click on RUN on the top tool bar.</p>
    </div>

    <div v-if="executionFlow" class="execution-flow-organism__container lc-custom-scroll">
      <p class="execution-flow-organism__container__viewlogs" @click="viewAllLogs">View Logs</p>

      <p>Response</p>
      <div class="execution-flow-organism__container__json lc-custom-scroll">
        <json-viewer v-if="executionResponse !== undefined" :value="executionResponse" expanded copyable theme="less-code-json-viewer">
          <el-tooltip slot="copy" class="item" effect="dark" content="Copy" placement="top-start">
            <el-button  size="mini"  circle icon="el-icon-document-copy"></el-button>
          </el-tooltip>
        </json-viewer>
      </div>
      <p v-if="responseHeaders.length > 0">Response Headers</p>
      <div v-if="responseHeaders.length > 0" class="execution-flow-organism__container__headers">
        
      </div>

      <p>Execution Flow</p>
      <div class="execution-flow-organism__container__flow">
        <el-timeline >
          <el-timeline-item :hide-timestamp="true" timestamp="End" placement="top">
            <el-card :body-style="{padding:'10px'}">
              <label>START</label>
            </el-card>
          </el-timeline-item>

          <el-timeline-item :hide-timestamp="true" v-for="(item) in executionFlowData" :key="item.blockId" placement="top" size="large" :type="item.errorOccured ? 'danger': ''">
            <el-card :body-style="{padding:'10px'}">

              <el-collapse>
                <el-collapse-item >
                  <template slot="title">
                    <label class="collapse-title">{{ item.caption }}</label>
                  </template>
                  <p><b>Block type </b>: {{getCaptionByBlockType(item.blockType)}}</p>
                  <p v-if="item.log"><b class="view-logs" @click="viewLogs(item.log)">View Logs</b></p>
                  <p><b>Elapsed Time </b>: {{item.executionTime}} ms</p>
                  <b>Inputs</b>
                  <json-viewer v-if="item.inputs" :value="item.inputs" expanded copyable theme="less-code-json-viewer"></json-viewer>
                  <b v-if="executionSuccess">Output</b>
                  <json-viewer v-if="executionSuccess && item.output" :value="item.output" copyable expanded theme="less-code-json-viewer"></json-viewer>
                  <p v-if="executionSuccess && (item.output !== undefined)">NULL</p>
                </el-collapse-item>
              </el-collapse>

            </el-card>
          </el-timeline-item>

          <el-timeline-item :hide-timestamp="true" timestamp="End" placement="top" :type="executionSuccess ? 'success' : 'danger'">
            <el-card :body-style="{padding:'10px'}">
              <label v-if="executionSuccess"><i class="el-icon-success"></i>SUCCESS (200)</label>
              <label v-if="!executionSuccess"><i class="el-icon-error"></i>ERROR ({{responseHeaders.responseCode}})</label>

            </el-card>           
          </el-timeline-item>

        </el-timeline>
      </div>
    </div>
    </div>
</template>

<script>

import Vue from 'vue'
import JsonViewer from 'vue-json-viewer'
Vue.use(JsonViewer);

import {EDITABLE_TYPES} from '../../../../../Common/VariousTypes';
import ExecutionFlowOrganism from './ExecutionFlowOrganism.hook'
const hook = new ExecutionFlowOrganism();

export default {
  name: 'ExecutionFlowOrganism',
  beforeCreate() { hook.hook(this)},
  methods: hook.methods("getBlockByType","getCaptionByBlockType","viewLogs","viewAllLogs"),
  props: { },
  data() {
      return { 
        EDITABLE_TYPES
      }
  },
  computed: {
    executionFlowData(){
      let flow = this.executionFlow?.executionFlow || [];
      flow.forEach(f=>f.expanded = false);
      return flow;
    },
    responseHeaders(){
      return this.executionFlow?.responseHeaders || [];
    },
    executionResponse(){
      return this.executionFlow?.response;
    },
    executionSuccess(){
      return this.executionFlow?.success || false;
    },
    ...hook.getters("selectedItems","executionFlow")
  }

}

</script>

<style scoped lang="scss">

.execution-flow-organism {
  height: calc(100vh - 100px);

  &__message {
    padding:20px;
    font-size:18px;
    opacity: 0.5;
  }

  &__container {
    
    height:calc(100% - 20px);
    overflow-y: scroll;
    padding-left:10px;
    padding-right:10px;

    &__viewlogs{
      text-decoration: underline;
      cursor: pointer;
    }

    &__json {
      
      margin-top:10px;
      max-height: 400px;
      overflow: auto;
      background-color: #1E1E1E;
      border-radius: 7px;
      margin-bottom:20px;
    }

    &__flow {
      margin-left:-20px;
      margin-top: 40px;

      i {
        margin-right:10px;
      }

      .el-collapse{
        border: 0px;
      }

      label.collapse-title {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        cursor: pointer;
      }

      b.view-logs{
        text-decoration: underline;
        cursor: pointer;
      }
      

    }

    &__responses {
      margin-left:20px;
    }

    &__headers {
      margin-top:10px;
    }
  }
}

.less-code-json-viewer {
  background: #1E1E1E;
  color: #a0a0a0;
  font-size: 14px;
  font-family: Consolas, Menlo, Courier, monospace;
  border: none;

  .jv-ellipsis {
    color: #999;
    background-color: #eee;
    display: inline-block;
    line-height: 0.9;
    font-size: 0.9em;
    padding: 0px 4px 2px 4px;
    border-radius: 3px;
    vertical-align: 2px;
    cursor: pointer;
    user-select: none;
  }
  .jv-button { color: #49b3ff }
  .jv-key { color: #111111 }
  .jv-item {
    &.jv-array { color: #111111 }
    &.jv-boolean { color: #fc1e70 }
    &.jv-function { color: #067bca }
    &.jv-number { color: #fc1e70 }
    &.jv-object { color: #111111 }
    &.jv-undefined { color: #e08331 }
    &.jv-string {
      color: #42b983;
      word-break: break-word;
      white-space: normal;
    }
  }
  .jv-code {
    .jv-toggle {
      &:before {
        padding: 0px 2px;
        border-radius: 2px;
      }
      &:hover {
        &:before {
          background: #eee;
        }
      }
    }
  }
}
   


</style>
